<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="/"></a>
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
      <!---->
      <el-row>
        <el-col :span="7">
          <el-input placeholder="" v-model="findinput" clearable>
            <el-button slot="append" icon="el-icon-search" @click="findgood()"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!---->
      <el-table :data="tableData" stripe border>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="routeid" label=""></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="carnumber" label=""></el-table-column>        
        <el-table-column prop="routestate" label=""></el-table-column>
        <el-table-column label="" width="230px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-search"
              @click="toRoute(scope.row)"
            ></el-button>          
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
//import axios from 'axios'
export default {
  data() {
    return {
      pageSize:'1',
      total:'3',
      findinput: '',
      tableData: [{routeid:'11'}],
      formLabelWidth: "120px"
    };
  },

   methods: {
    //
    toRoute(row) {
      this.$router.push({
        path:'/goodroutesearch',
        query:{
          routeid:row.routeid
        }
      });
    },

    //id
    findgood(){
      const _this = this
      if(this.findinput != ''){
        axios.get('http://localhost:8282/route/findVRouteByRouteid/'+this.findinput).then(function(resp){
        console.log(resp)
        _this.tableData = resp.data;
        })
      }else{
        const _this = this;
        axios.get('http://localhost:8282/route/findVRoute').then(function(resp) {
        console.log(resp);
        _this.tableData = resp.data;
      });
      } 
    },
  },

  created() {
    const _this = this;
    axios.get('http://localhost:8282/route/findVRoute').then(function(resp) {
        console.log(resp);
        _this.tableData = resp.data;
      });
  }   
}
</script>

<style lang="sass" scoped>
</style>