<template>
  <div>
    <el-card>
      
      ()
      <el-input class="goodweight" style="width: 120px;" v-model="goodweight" readonly></el-input>&#8195;&#8195;
      (m)
      <el-input
        id="goodvolume"
        name="goodvolume"
        style="width: 120px;"
        v-model="goodvolume"
        readonly
      ></el-input>&#8195;&#8195;
      ()
      <el-input style="width: 120px;" v-model="goodnum" readonly></el-input>&#8195;&#8195;
      ()
      <el-input style="width: 120px;" v-model="goodtotal" readonly></el-input>&#8195;&#8195;
      <el-button type="primary" icon="el-icon-truck" @click="testgoodweight"></el-button>
      <br />
      <br />
      <el-input v-model="distcaraddr" style="display:none"></el-input>
      :
      <el-input style="width: 120px;" v-model="distcarid" readonly></el-input>&#8195;&#8195;
      
      <el-input style="width: 120px;" v-model="distcarnumber" readonly></el-input>&#8195;&#8195;
      ()
      <el-input style="width: 120px;" v-model="weight" readonly></el-input>&#8195;&#8195;
      (m)
      <el-input style="width: 120px;" v-model="volume" readonly></el-input>&#8195;&#8195;
      <br />
      <br />
      <el-select v-model="startlbsfind" clearable placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>&#8195;&#8195;
      
      <el-select v-model="endlbsfind" clearable placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>&#8195;&#8195;
      <el-button type="primary" icon="el-icon-search" @click="findgood()"> </el-button>
      <el-button type="warning" icon="el-icon-check" @click="todist"></el-button>
      <br />
      <el-link type="success" href="/#/lbs"></el-link>
      <br />
      <!---->
      <el-table
        ref="multipleTable"
        :data="tableData1"
        height="300"
        border
        tooltip-effect="dark"
        @selection-change="handleSelectionChange"
        @row-click="choosegood"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="goodid" label=""></el-table-column>
        <el-table-column prop="goodweight" label="()"></el-table-column>
        <el-table-column prop="goodvolume" label="(m)"></el-table-column>
        <el-table-column prop="goodnum" label=""></el-table-column>
        <el-table-column prop="startaddr" label=""></el-table-column>
        <el-table-column prop="endaddr" label=""></el-table-column>
        <el-table-column prop="startlbs" label=""></el-table-column>
        <el-table-column prop="endlbs" label=""></el-table-column>
        <el-table-column prop="goodtime" label=""></el-table-column>
        <el-table-column prop="goodstate" label=""></el-table-column>
      </el-table>
      <br />
      <br />
      <!---->
      <el-input style="width:20%" placeholder="" v-model="findcarnumber" clearable></el-input>&#8195;&#8195;
      <el-select v-model="carlbsfind" clearable placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>&#8195;&#8195;
      <el-button type="primary" icon="el-icon-search" @click="findcar()"> </el-button>&#8195;&#8195;
      <el-button type="primary" @click="setCurrent()"></el-button>

      <!---->
      <el-table
        ref="singleTable"
        :data="tableData2"
        height="250"
        border
        highlight-current-row
        @current-change="handleCurrentChange"
        @row-click="choosecar"
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="carid" label=""></el-table-column>
        <el-table-column prop="carnumber" label=""></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="cartype" label=""></el-table-column>
        <el-table-column prop="carvehicle" label="()"></el-table-column>
        <el-table-column prop="carvolume" label="(m)"></el-table-column>
        <el-table-column prop="carrate" label="(X100%)" width="130px"></el-table-column>
        <el-table-column prop="caraddr" label=""></el-table-column>
        <el-table-column prop="carlbs" label=""></el-table-column>
        <el-table-column prop="carway" label=""></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData1: [
        {
          goodid: "111",
          goodweight: "15",
          goodvolume: "1",
          goodnum: "23"
        },
        {
          goodid: "111",
          goodweight: "13",
          goodvolume: "0.5",
          goodnum: "23"
        }
      ],
      tableData2: [],
      distcaraddr:"",
      distcarnumber: "",
      distcarid: "",
      weight: "0",
      volume: "0",
      goodweight: "0",
      goodvolume: "0",
      goodnum: "0",
      goodtotal: "0",
      startlbsfind: "",
      endlbsfind: "",
      carlbsfind: "",
      findcarnumber:"",
      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      multipleSelection: []
    };
  },

  methods: {
    //
    findgood(){
      if(this.startlbsfind != '' && this.endlbsfind ==''){
        const _this = this;
        axios.get('http://localhost:8282/good/findGoodByStartlbs/'+this.startlbsfind).then(function(resp) {
          console.log(resp);
          _this.tableData1 = resp.data;
        });
      }else if(this.startlbsfind == '' && this.endlbsfind !=''){
        const _this = this;
        axios.get('http://localhost:8282/good/findGoodByEndlbs/'+this.endlbsfind).then(function(resp) {
          console.log(resp);
          _this.tableData1 = resp.data;
        });
      }else if(this.startlbsfind != '' && this.endlbsfind !=''){
        const _this = this;
        axios.get('http://localhost:8282/good/findGoodByStartlbsAndEndlbs/'+this.startlbsfind+'&'+this.endlbsfind)
          .then(function(resp) {
          console.log(resp);
          _this.tableData1 = resp.data;
        });
      }else if(this.startlbsfind == '' && this.endlbsfind ==''){
        const _this = this;
        axios.get('http://localhost:8282/good/findGoodByGoodstate').then(function(resp) {
          console.log(resp);
          _this.tableData1 = resp.data;
        });
      }
    },

    //
    findcar(){
      if(this.findcarnumber != '' && this.carlbsfind == ''){
        const _this = this;
        axios.get('http://localhost:8282/car/findCarByCarnumber/'+this.findcarnumber).then(function(resp) {
          console.log(resp);
          _this.tableData2 = resp.data;
        });
      }else if(this.findcarnumber == '' && this.carlbsfind != ''){
        const _this = this;
        axios.get('http://localhost:8282/car/findCarByCarlbs/'+this.carlbsfind).then(function(resp) {
          console.log(resp);
          _this.tableData2 = resp.data;
        });
      }else if(this.findcarnumber != '' && this.carlbsfind != ''){
        const _this = this;
        axios.get('http://localhost:8282/car/findCarByCarnumberAndCarlbs/'+this.findcarnumber+'&'+this.carlbsfind).then(function(resp) {
          console.log(resp);
          _this.tableData2 = resp.data;
        });
      }else if(this.findcarnumber == '' && this.carlbsfind == ''){
        const _this = this;
        axios.get('http://localhost:8282/car/findCarByCarway').then(function(resp) {
          console.log(resp);
          _this.tableData2 = resp.data;
        });
      }
    },

    //
    handleSelectionChange(val) {
      const _this = this;
      var sumweight = 0;
      var sumvolume = 0;
      var countnum = 0;
      var sumtotal = 0;
      _this.multipleSelection.length = 0;
      _this.multipleSelection = val;
      console.log(_this.multipleSelection);
      for (var i in _this.multipleSelection) {
        sumweight +=
          _this.multipleSelection[i].goodweight *
          _this.multipleSelection[i].goodnum;
        sumvolume +=
          _this.multipleSelection[i].goodvolume *
          _this.multipleSelection[i].goodnum;
        sumtotal =
          parseInt(sumtotal) + parseInt(_this.multipleSelection[i].goodnum);
        countnum = ++i;
      }
      this.goodweight = sumweight;
      this.goodvolume = sumvolume;
      this.goodnum = countnum;
      this.goodtotal = sumtotal;
    },
    choosegood(row) {
      this.$refs.multipleTable.toggleRowSelection(row);
      // console.log(row.goodid);
    },

    //
    setCurrent(row) {
      this.$refs.singleTable.setCurrentRow(row);
      this.distcarid = "";
      this.distcarnumber = "";
      this.weight = 0;
      this.volume = 0;
    },
    handleCurrentChange(val) {
      this.currentRow = val;
    },
    choosecar(row) {
      this.$refs.singleTable.setCurrentRow(row);
      this.distcaraddr = row.caraddr;
      this.distcarid = row.carid;
      this.distcarnumber = row.carnumber;
      this.weight = row.carvehicle * 1000 * row.carrate;
      this.volume = row.carvolume * row.carrate;
    },

    //
    testgoodweight() {
      if (this.goodweight <= this.weight && this.goodvolume <= this.volume) {
        this.$message({
          type: "success",
          message: ""
        });
      } else {
        this.$message({
          type: "error",
          message: ""
        });
      }
    },

    //
    todist() {
     console.log(this.multipleSelection)
      if (this.goodweight <= this.weight && this.goodvolume <= this.volume && this.goodweight!=0) {
        this.$confirm(
          "" +
            this.distcarnumber +
            ", ?",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          }
        )
          .then(() => {
            const _this = this;
            axios.post('http://localhost:8282/route/insertRoute/'+this.distcarid+'&'+this.goodweight
            +'&'+this.goodvolume+'&'+this.goodnum+'&'+this.goodtotal).then(function(resp) {
                   console.log(resp.data);
                   if(resp.data == "success"){
                     axios.get('http://localhost:8282/route/findRouteByRoutetime').then(function(res) {
                      console.log(res);
                      if(res.status == 200){
                        for(var i=0;i<_this.multipleSelection.length;i++){
                          axios.put('http://localhost:8282/good/updateGoodRouteid/'+res.data[0].routeid+'&'
                           +_this.multipleSelection[i].goodid).then(function(re) {
                            console.log(re);
                          });
                        }
                         axios.put('http://localhost:8282/car/updateCarwayToOne/'+_this.distcarid).then(function(resp1) {
                            console.log(resp1);
                          });
                         _this.$router.push({
                            path: "/goodrouteplan",
                            query: {
                              caraddr: _this.distcaraddr,
                              carnumber: _this.distcarnumber,
                              goodweight: _this.goodweight,
                              goodvolume: _this.goodvolume,
                              goodnum: _this.goodnum,
                              startaddr: _this.startaddr,
                              multipleSelection: _this.multipleSelection
                            }
                         });
                        _this.$message({
                          type: "success",
                          message: ""
                        }); 
                      }
                    });
                   }else{
                     _this.$message({
                         type: "error",
                         message: ""
                      });
                   }   
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      } else {
        this.$message({
          type: "error",
          message: ""
        });
      }
    },
  },

  created(){
    const _this = this;
    axios.get('http://localhost:8282/good/findGoodByGoodstate').then(function(resp) {
        console.log(resp);
        _this.tableData1 = resp.data;
      });
    axios.get('http://localhost:8282/car/findCarByCarway').then(function(resp) {
        console.log(resp);
        _this.tableData2 = resp.data;
    });
  }
};
</script>