<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="/"></a>
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
       <!---->
      <el-row>
        <el-col :span="7">
          <el-input placeholder="" v-model="findinput" clearable>
            <el-button slot="append" icon="el-icon-search" @click="findroute()"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!---->
      <el-table ref="singleTable"
        :data="tableData1"
        height="250"
        stripe
        border
        highlight-current-row
        @current-change="handleCurrentChange"
        @row-click="chooseroute"
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="routeid" label=""></el-table-column>
        <el-table-column prop="carnumber" label=""></el-table-column>
        <el-table-column prop="routeweight" label=""></el-table-column>
        <el-table-column prop="routevolume" label=""></el-table-column>
        <el-table-column prop="routenum" label=""></el-table-column>
        <el-table-column prop="routetotal" label=""></el-table-column>
        <el-table-column prop="routetime" label=""></el-table-column>
        <el-table-column prop="routestate" label=""></el-table-column>
      </el-table>
      <br/>
      <!---->
      <el-table
        :data="tableData2"
        height="250"
        border
      >
        <el-table-column prop="goodid" label=""></el-table-column>
        <el-table-column prop="goodweight" label="()"></el-table-column>
        <el-table-column prop="goodvolume" label="(m)"></el-table-column>
        <el-table-column prop="goodnum" label=""></el-table-column>
        <el-table-column prop="startaddr" label=""></el-table-column>
        <el-table-column prop="endaddr" label=""></el-table-column>
        <el-table-column prop="startlbs" label=""></el-table-column>
        <el-table-column prop="endlbs" label=""></el-table-column>
        <el-table-column prop="goodtime" label=""></el-table-column>
        <el-table-column prop="goodstate" label=""></el-table-column>
      </el-table>
     
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      findinput:"",
      tableData1:[],
      tableData2:[],
    }
  },
  methods:{
    //
    setCurrent(row) {
      this.$refs.singleTable.setCurrentRow(row);
      this.distcarid = "";
      this.distcarnumber = "";
      this.weight = 0;
      this.volume = 0;
    },
    handleCurrentChange(val) {
      this.currentRow = val;
    },
    chooseroute(row) {
      this.$refs.singleTable.setCurrentRow(row);
      const _this = this;
        axios.get('http://localhost:8282/good/findGoodByRouteid/'+row.routeid).then(function(resp) {
          console.log(resp);
          _this.tableData2 = resp.data;
        });
    },

    //
    findroute(){
      if(this.findinput != ''){
        const _this = this;
        axios.get('http://localhost:8282/route/findVRouteByRouteid/'+this.findinput).then(function(resp) {
          console.log(resp);
          _this.tableData1 = resp.data;
        });
      }else{
        const _this = this;
        axios.get('http://localhost:8282/route/findVRouteByRouteAndUser/'+this.$session.get("useraccount")).then(function(resp) {
          console.log(resp);
          _this.tableData1 = resp.data;
        });
      }
    }
  },

  created(){
    const _this = this;
    axios.get('http://localhost:8282/route/findVRouteByRouteAndUser/'+this.$session.get("useraccount")).then(function(resp) {
        console.log(resp);
         _this.tableData1 = resp.data;
    });
  }
}
</script>