<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="/"></a>
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
      <el-input style="width: 20px;top:5px;" v-model="routeidcheck" readonly type="hidden"></el-input>
      <!---->
      <el-table :data="tableData" stripe border>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="routeid" label=""></el-table-column>
        <el-table-column prop="carid" label=""></el-table-column>
        <el-table-column prop="carnumber" label=""></el-table-column>
        <el-table-column prop="routeweight" label=""></el-table-column>
        <el-table-column prop="routevolume" label=""></el-table-column>
        <el-table-column prop="routenum" label=""></el-table-column>
        <el-table-column prop="routetotal" label=""></el-table-column>
        <el-table-column prop="routetime" label=""></el-table-column>
        <el-table-column prop="routestate" label=""></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-check"
              @click="tojiedan(scope.row)"
              :disabled="scope.row.routestate == ''"
            ></el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-close"
              @click="tojujue"
              :disabled="scope.row.routestate == ''"
            ></el-button>
            <el-button
              size="mini"
              type="success"
              icon="el-icon-s-promotion"
              @click="toTrans(scope.row)"
              :disabled="scope.row.routestate == ''"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

     <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible2" width="30%" >
        <el-input
            type="textarea"
            :rows="5"
            placeholder=""
            v-model="textarea"
            maxlength="500"
            show-word-limit
            clearable>
         </el-input>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogVisible2 = false"> </el-button>
          <el-button type="primary" @click="dialogjujue"> </el-button>
        </span>
      </el-dialog>
  </div>
</template>
<script>
//import axios from 'axios'
export default {
  data() {
    return {
      textarea:'',
      findinput: "",
      tableData: [{}],
      formLabelWidth: "120px",
      dialogVisible2:false,
      routeidcheck:'',
    };
  },

  methods: {
    //
    tojiedan(row){
      const _this = this;
      axios.put("http://localhost:8282/route/updateRoutestateDeal/" + row.routeid)
        .then(function(resp) {
          console.log(resp.data);
          if (resp.data === "success") {
            axios.get("http://localhost:8282/route/findVRouteByUser/" +_this.$session.get("useraccount")).then(function(res) {
              console.log(res);
              _this.tableData = res.data;
            });
            _this.$message({
              type: "success",
              message: ""
            });
          }
        });
    },
    //
    tojujue(row) {
      this.routeidcheck = row.routeidid;
      this.dialogVisible2 = true;
        
    },
    dialogjujue(){
      this.$message({
          type: "success",
          message: ""
        });
      this.dialogVisible2 = false;
    },
    //
    toTrans(row) {
        this.$router.push({
            path: "/routedelivery",
            query: {
              routeid: row.routeid
            }
        });
    },

  },

  created() {
    const _this = this;
    axios
      .get(
        "http://localhost:8282/route/findVRouteByUser/" +
          this.$session.get("useraccount")
      )
      .then(function(resp) {
        console.log(resp);
        _this.tableData = resp.data;
      });
  }
};
</script>
<style lang="sass" scoped>
</style>